import {Person} from './models/person'
export class Teacher extends Person{
    salary: number;
    constructor(firstName:string, lastName:string, salary: number){
        firstName = "TEC-" + firstName;
        lastName = "TEC-" + lastName;
        let age = 40;
        super(firstName,lastName,age);
        this.salary = salary;
    }
}


